<script setup>
import { reactive, ref } from 'vue';
let tr = ref(false);
const list = ref([
    {
        id: 1,
        img: '/begin/images/aeropostale.jpg',
        alt: 'aeropostale',
    },{
        id: 2,
        img: '/begin/images/forever.jpg',
        alt: 'forever'
    },{
        id: 3,
        img: '/begin/images/abercrombie.jpg',
        alt: 'abercrombie'
    },{
        id: 4,
        img: '/begin/images/nike.jpg',
        alt: 'nike'
    },{
        id: 5,
        img: '/begin/images/hollister.jpg',
        alt: 'hollister'
    },
]);

function prev() {
    let lastItem = list.value[list.value.length - 1];
    
    list.value.pop();
    setTimeout(() => {
        list.value.unshift(lastItem);
    }, 0)
    
}

function next() {
    let firstItem = list.value[0];
    list.value.shift();
    setTimeout(() => {
        list.value.push(firstItem);
    }, 0);
    // for(let i = 0; i < list.value.length; i++) {
        
    // }

    console.log(list.value.length / 2 + 1);
}
</script>

<template>
    <section class="section looped-carousel theme-item">
        <div class="container">
            <div class="carousel">
                <h3 class="h3 carousel__header underlined text-center">Cross store shopping in real time and cloth simulation on your own character!</h3>
                    <div class="carousel__inner">
                        <TransitionGroup tag="ul" name="fade" class="carousel__list flex jc-center" mode="in-out">
                            <li v-for="item, i in list" :key="item"
                            class="carousel__item"
                            :class="{'scaled': i === 2}">
                                <img :src="item.img" :alt="item.alt" class="carousel__img">
                            </li>
                        </TransitionGroup>
                        <button @click="prev" class="carousel__btn carousel__btn_prev">
                            <ion-icon name="chevron-back-outline"></ion-icon>
                        </button>
                        <button @click="next" class="carousel__btn carousel__btn_next">
                            <ion-icon name="chevron-forward-outline"></ion-icon>
                        </button>
                    </div>
            </div>
        </div>
    </section>
</template>

