<script setup>
import { ref } from 'vue';
const carouselList = [
    {
        id: 1,
        src: '/begin/images/about-carousel-1.jpg',
        title: 'Lorem Ipsum',
        description: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod',
        alt: 'Lorem Ipsum'
    },
    {
        id: 2,
        src: '/begin/images/about-carousel-2.jpg',
        title: 'Lorem Ipsum',
        description: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod',
        alt: 'Lorem Ipsum'
    },
    {
        id: 3,
        src: '/begin/images/about-carousel-3.jpg',
        title: 'Lorem Ipsum',
        description: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod',
        alt: 'Lorem Ipsum'
    },
];

let currentIndex = ref(0)

function next() {
    if(currentIndex.value === carouselList.length - 1) {
        currentIndex.value = 0;
    } else {
        currentIndex.value++;
    }
    
}

function prev() {
    if(currentIndex.value === 0) {
        currentIndex.value = carouselList.length - 1
    } else {
        currentIndex.value--;
    }
}
</script>
<template>
    <div class="about-carousel">
        <div class="about-carousel__indicators">
                <div v-for="(item, index) in carouselList" 
                :key="item.id"
                :class="[
                    {'active': index === currentIndex}, 
                    'about-carousel__indicator'
                ]"></div>
        </div>
        <div class="about-carousel__inner">
            <ul class="about-carousel__list">
                <template v-for="(item, index) in carouselList" :key="item.id">
                    <li :class="[
                        'about-carousel__item',
                        {'active': index === currentIndex}
                        ]">
                        <figure class="about-carousel__figure">
                            <img :src="item.src" :alt="item.alt" class="about-carousel__img">
                            <figcaption class="about-carousel__caption text-center">
                                <h6 class="h6 about-carousel__title metropolis">{{ item.title }}</h6>
                                <p class="about-carousel__text">{{ item.description }}</p>
                            </figcaption>
                        </figure>
                    </li>
                </template>
            </ul>
            <div class="about-carousel__buttons">
                <button @click="prev()"
                class="about-carousel__button about-carousel__button_prev">
                    <ion-icon name="chevron-back-outline"></ion-icon>
                </button>
    
                <button @click="next()"
                class="about-carousel__button about-carousel__button_next">
                    <ion-icon name="chevron-forward-outline"></ion-icon>
                </button>
            </div>
        </div>
    </div>
</template>

